.hamburger__mobile-menu, .submenu {
  height: calc(100vh - 50px);
  width: 100%;
  display: none;
  position: absolute;
  top: 50px;
  left: 0;
}

.hamburger__mobile-menu li {
  margin-bottom: 10px;
}

.hamburger__mobile-menu li:hover, header nav ul li:hover, header h1 a:hover {
  transition: all .3s;
  transform: scale(1.1);
}

.hamburger__mobile-menu .nombre {
  margin-top: 25px;
  font-size: large;
}

.hamburger__mobile-menu a {
  text-decoration: none;
}

.hamburger-icon {
  cursor: pointer;
  margin: auto 0;
  display: none;
}

.hamburger-icon div {
  width: 35px;
  height: 3px;
  background-color: red;
  margin: 6px 0;
  transition: all .4s;
}

.open .hamburger-icon__bar1 {
  transform: rotate(-45deg)translate(-6px, 6px);
}

.open .hamburger-icon__bar2 {
  opacity: 0;
}

.open .hamburger-icon__bar3 {
  transform: rotate(45deg)translate(-6px, -8px);
}

.open .hamburger__mobile-menu {
  height: 200px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.hamburger-icon .open {
  height: 220px;
}

@media only screen and (max-width: 768px) {
